<template>
  <div id="account_setting">
    <div class="block_sup_container">
      <div class="title">账户设置</div>
    </div>
    <div class="block_sub_container">
      <div class="page_user_center">
        <Row>
          <Col :xs="{ span: 5, offset: 3 }" align="right" class-name="left">用户名</Col>
          <Col :xs="{ span: 10, offset: 2 }">admin</Col>
        </Row>
        <Row>
          <Col :xs="{ span: 5, offset: 3 }" align="right" class-name="left">头像</Col>
          <Col :xs="{ span: 10, offset: 2 }">
            <span class="tip">仅支持JPG、PNG格式，文件小于1M(方形图)</span>
            <label for="upload">
                <div class="img_round">
                <img src="@/assets/img/avatar_round.png" alt="" width="125" height="125">
                <div class="upload">
                <img src="@/assets/img/upload.svg"  alt="" width="45" height="45">
                <span>上传图片</span>
              </div>
            </div>
              <input type="file" id="upload" accept="image/jpg, image/png">
            </label>
          </Col>
        </Row>
        <Row>
          <Col :xs="{ span: 5, offset: 3 }" align="right" class-name="left">个人描述</Col>
          <Col :xs="{ span: 10, offset: 2 }">
            <Input v-model="description" type="textarea" :rows="4" :maxlength="120"></Input>
          </Col>
        </Row>
        <Row>
          <Col :xs="{ span: 5, offset: 3 }" align="right" class-name="left">邮箱</Col>
          <Col :xs="{ span: 10, offset: 2 }">
            <Input v-model="email"  style="width: 300px"></Input>
          </Col>
        </Row>
        <Row>
          <Col span="24" align="center">
            <Button type="primary" size="large">保存</Button>
          </Col>
        </Row>
      </div>
    </div>
  </div>
</template>

<script>
import {updateAvatar} from '@/network/index/index'

export default {
  data() {
    return {
      description: '',
      email: '',
      avatar: ''
    }
  },
  mounted() {
    this.openFile()
  },
  methods: {
    openFile() {
      const el = document.getElementById('upload')
      el.addEventListener('change', e => {
        const data = e.target.files
        if (data[0].type != 'image/jpeg' || data[0].type != 'image/png') {
          this.$toast.show('仅支持JPG、PNG格式图片')
          return
        }
        this.avatar = data
      })
    },
    updateInfo() {
      updateAvatar(this.avatar).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style lang="less" scoped>
#account_setting {
  width: 100%;
  .block_sup_container  {
    // margin: 0 auto;
    position: relative;
    z-index: 10;
    // display: flex;
    flex-wrap: wrap;
    background-color: transparent;
    .title {
      margin: 30px 30px;
      padding-left: 25px;
      font-size: 22px;
      color: var(--theme-color);
      vertical-align: baseline;
      display: inline-block;
    }
  }
  .block_sub_container {
    width: 100%;
    height: calc(100vh - 135px);
    background: transparent;
    box-sizing: border-box;
    .page_user_center {
      width: 800px;
      // margin: auto;
      background: transparent;
      padding-top: 40px;
      padding-bottom: 20px;
      .tip {
        color: #999;
      }
      .img_round {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 140px;
        height: 140px;
        margin: 6px;
        border-radius: 50%;
        background-color: #0a0f30;
        overflow: hidden;
        cursor: pointer;
        img {
          margin-top: 25px;
        }
        .upload {
          position: absolute;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-color: rgba(100, 100, 100, 0.35);
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          color: #fff;
          font-size: 16px;
          opacity: 0;
          img {
            margin-top: 0;
          }
        }
        .upload:hover{
          opacity: 1;
        }
      }
      .ivu-row {
        padding: 12px 0;
        .left {
          color: var(--theme-color);
        }
      }
    }
  }
  
}
</style>